import $ from 'jquery';
import 'slick-carousel';

$(document).ready(function() {
  $('.slick-carousel').slick();
  const hamburger = $('.hamburger');
  const body = $('body');
  hamburger.on('click', function(e) {
    hamburger.toggleClass('is-active');
    // body.toggleClass('nav-is-active');
  });
});



